import React from "react";
import LocationAndCategory from "./tabs/LocationAndCategory";
import TopicContent from "./tabs/TopicContent";
import Media from "./tabs/Media";
import Preview from "./tabs/Preview";
import checkMark from "../../assets/images/check-mark.png";
import "./style.scss";
class TopicSuggestion extends React.Component {
  state = {
    page: 1,
    checkMark: false
  };

  moveNextPage = page => {
    console.log(page);
    if (page === 1) {
      this.setState({
        page: 2,
        checkMark: true
      });
    } else if (page === 2) {
      this.setState({
        page: 3,
        checkMark: true
      });
    } else if (page === 3) {
      this.setState({
        page: 4,
        checkMark: true
      });
    } else {
      this.setState({
        page: 5,
        checkMark: true
      });
    }
  };

  render() {
    return (
      <div className="suggest-topic">
        <div className="tabs">
          <ul>
            <li>
              Category and <br />
              location{" "}
              {this.state.page === 1 && this.state.checkMark && (
                <img src={checkMark} alt="check-mark" />
              )}{" "}
            </li>
            <li>
              Topic content
              {this.state.page === 2 && this.state.checkMark && (
                <img src={checkMark} alt="check-mark" />
              )}
            </li>
            <li>
              Media
              {this.state.page === 3 && this.state.checkMark && (
                <img src={checkMark} alt="check-mark" />
              )}
            </li>
            <li>
              Preview
              {this.state.page === 4 && this.state.checkMark && (
                <img src={checkMark} alt="check-mark" />
              )}
            </li>
          </ul>
        </div>
        <div className="tab-content">
          {this.state.page === 1 ? (
            <LocationAndCategory moveNextPage={() => this.moveNextPage(1)} />
          ) : this.state.page === 2 ? (
            <TopicContent moveNextPage={() => this.moveNextPage(2)} />
          ) : this.state.page === 3 ? (
            <Media moveNextPage={() => this.moveNextPage(3)} />
          ) : (
            <Preview moveNextPage={() => this.moveNextPage(4)} />
          )}
        </div>
      </div>
    );
  }
}

export default TopicSuggestion;
